*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    overflow: hidden;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}
.img100{width:100%;height:100%;position: absolute;left:0;top:0;display: block;z-index:1000;color:#fff;}
.bottom-col{position: relative;}
.list div{font-size:4.8vw;display: inline-block;position: absolute;border-radius: 2.13vw;line-height:9.06vw;white-space: nowrap;
padding:0 4vw;}
.list div:before{
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    width:1.86vw;
    height:1.86vw;
    display: block;position: absolute;
    left:4vw;top:-4vw;
    content: "";
}
.list div.active{
    background: #7D33FF;
}
.list div.active:before{
    background: #fff;
}


.list div:nth-child(1){
    right:0;
    top:36%;
}
.list div:nth-child(2){
    left:0;
    top:46%;
}
.list div:nth-child(3){
    right:0;
    top:54%;
}

.list div:nth-child(4){
    left:2%;
    top:60%;
}

.bottom-col{position: absolute;bottom:13.87vw;width:100%;text-align: center;}
.bottom-col .text{font-size:2.667vw;padding-bottom:4vw;}
.text-col{display: block;position: absolute;left:5.16vw;top:28vw;}
.no-col{
    position: absolute;left:5.16vw;top:10vw;
}
.no-col .count{width:10.2vw;margin-left:2vw;}
.no-col .no{
    width:27.8vw;
}
.bottom-col img{width:15.29vw;}
.content{opacity: 0;transition: all 2s;position: absolute;width:100%;height:100%;top:0;left:0;z-index:-1;}
.content.active{opacity: 1;z-index:1;}

.bottom-icon{position: absolute;width:100%;left:0;bottom:0;z-index:1000;}
.bottom-icon img{width:3vw;position: absolute;bottom:16vw;z-index:1000;}
.bottom-icon .left{left:5.2vw;}
.bottom-icon .right{right:5.2vw;}
